import React from "react";
import s from './Dialog.module.css'
import {DialogItem} from "./DialogsItem/DialogsItem";
import {Messages} from "./DialogMessage/Messages";
import { dialogsPageType } from "../../../Redux/State";




type dialogsPageProps = {
    dialogsPage: dialogsPageType
}

export function Dialogs(props: dialogsPageProps) {

    let dialogsElement = props.dialogsPage.dialogs.map(dialog => <DialogItem name={dialog.name} id={dialog.id} img={dialog.img}/>)

    let messagesElement = props.dialogsPage.messages.map(message => <Messages message={message.message}/>)

    return (
        <div className={s.dialogs}>
            <div className={s.dialogsItems}>
                {dialogsElement}
            </div>

            <div className={s.messages}>
                {messagesElement}
            </div>
        </div>
    )
}

export default Dialogs